body {
  background: linear-gradient(30deg, #94ecd9, #7c69b9, #b175a3);
}

P {
  letter-spacing: 1.3px;
  text-align: center;
  text-shadow: 8px 8px 3px rgba(0, 0, 0, .2);
  line-height: 2;
}

.container {
  max-width: 1200px;
  border: 8px double #4a86ac;
  margin: 90px auto;
  padding: 20px 20px 40px;
  box-shadow: 8px 8px 3px rgba(0, 0, 0, .2);
}

.list {
  width: 25%;
  height: auto;
  padding-bottom: 20px;
}

.hidden {
  display: none;
}

.cat-info {
  text-shadow: 8px 8px 3px rgba(0, 0, 0, .2);
  text-align: center;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  display: flex;
}

.cat-info img {
  width: 400px;
  height: 300px;
  border: 5px double #4a86ac;
  box-shadow: 8px 8px 3px rgba(0, 0, 0, .2);
}

.loader-container {
  height: 300px;
  width: 300px;
  background-color: #4a86ac;
  border-radius: 35%;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  margin: 0 auto;
  display: flex;
  box-shadow: 8px 8px 3px rgba(0, 0, 0, .2);
}

.loader-animation {
  color: #fff;
  width: 1em;
  height: 1em;
  box-sizing: border-box;
  background-color: currentColor;
  border-radius: 50%;
  font-size: 48px;
  position: relative;
  transform: rotateX(-60deg)perspective(1000px);
}

.loader-animation:before, .loader-animation:after {
  content: "";
  box-sizing: border-box;
  width: inherit;
  height: inherit;
  border-radius: inherit;
  animation: flowerFlow 1s ease-out infinite;
  display: block;
  position: absolute;
  top: 0;
  left: 0;
}

.loader-animation:after {
  animation-delay: .4s;
}

@keyframes flowerFlow {
  0% {
    opacity: 1;
    transform: rotate(0deg);
    box-shadow: 0 0 0 -.5em, 0 0 0 -.5em, 0 0 0 -.5em, 0 0 0 -.5em, 0 0 0 -.5em, 0 0 0 -.5em, 0 0 0 -.5em, 0 0 0 -.5em;
  }

  100% {
    opacity: 0;
    transform: rotate(180deg);
    box-shadow: -1em -1em 0 -.35em, 0 -1.5em 0 -.35em, 1em -1em 0 -.35em, -1.5em 0 0 -.35em, 1.5em 0 0 -.35em, -1em 1em 0 -.35em, 0 1.5em 0 -.35em, 1em 1em 0 -.35em;
  }
}

.error-text {
  color: #840a0a;
  text-align: center;
  padding: 10px;
}

.error-container {
  letter-spacing: 1px;
  color: #fff;
  text-shadow: 0 0 1px #fff;
  padding-top: 15px;
  font-family: Arial, Helvetica, sans-serif;
  font-size: 30px;
  font-weight: bold;
  animation: smokeOut 1s ease-in-out infinite alternate;
  box-shadow: 8px 8px 3px rgba(0, 0, 0, .2);
}

.error-container:before {
  content: "Use key F5";
}

@keyframes smokeOut {
  100% {
    opacity: .08;
    filter: blur(5px);
    letter-spacing: 4px;
  }
}

